<template>
  <div class="addPopular">
    <h3>添加舆情指数</h3>
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
    
      <el-form-item label="看多指数" prop="more">
        <el-col :span="4">
          <el-input v-model="ruleForm.more"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="看空指数" prop="less">
        <el-col :span="4">
          <el-input v-model="ruleForm.less"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="观望指数" prop="shock">
        <el-col :span="4">
          <el-input v-model="ruleForm.shock"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="舆情指数" prop="percent">
        <el-col :span="4">
          <el-input v-model="ruleForm.percent"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="发布时间" required>
        <el-col :span="4">
          <el-form-item prop="date">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="ruleForm.date"
              style="width: 100%;"
            ></el-date-picker>
          </el-form-item>
        </el-col>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      typeList: ["看空", "震荡", "看多"],
      ruleForm: {
        date: "",
        more: "",
        less: "",
        shock: "",
        percent: ""
      },
      rules: {
        more: [
          { required: true, message: "请输入看多指数", trigger: "blur" }
        ],
        less: [
          { required: true, message: "请输入看空指数", trigger: "blur" }
        ],
        shock: [
          { required: true, message: "请输入观望指数", trigger: "blur" }
        ],
        percent: [
          { required: true, message: "请输入舆情指数", trigger: "blur" }
        ],
         
        date: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ],
      }
    };
  },
  methods: {
    format(value) {
      let date = new Date(value);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      return y + "-" + m + "-" + d;
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = {
            date: this.format(this.ruleForm.date),
            more: this.ruleForm.more,
            less: this.ruleForm.less,
            shock: this.ruleForm.shock,
            percent: this.ruleForm.percent,
          };
          this.$x.post(this.url.percentadd, params).then(res => {
            if (res.status) {
              this.$message({
                message: '添加成功',
                type: "success"
              });
              this.$router.push("/percentList");
            }
          });
          console.log(params);
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  }
};
</script>
<style  scoped>
.addPopular {
  border: 1px solid rgb(238, 238, 238);
  height: 100%;
  padding: 20px;
}
.addPopular h3 {
  margin-bottom: 20px;
  font-family: "Arial Negreta", "Arial Normal", "Arial";
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  height: 22px;
  line-height: 22px;
}
.form-group {
  margin-bottom: 10px;
}
.control-gtoup,
.control-label {
  float: left;
  line-height: 36px;
  margin-right: 20px;
}
.control-gtoup .el-input {
  width: 200px;
}

.control-label {
  width: 70px;
  text-align: right;
  margin-right: 10px;
}
.control-gtoup_w {
  width: calc(100% - 104px);
}
.form-group span {
  line-height: 38px;
  color: #666666;
}
.control-gtoup-table {
  width: calc(100% - 104px);
  padding: 20px;
}
.btn-box {
  text-align: center;
}
.el-button {
  margin-top: 30px;
  width: 123px;
}
</style>